import { Client } from 'boardgame.io/react';
import { Game } from 'boardgame.io/core';
import { AI } from 'boardgame.io/ai';
import dcopy from 'deep-copy';
import compare from 'just-compare';

import TicTacToeBoard from './board.js';

const diagonal = arr => {
  let diag = [];

  for (let pos = 0; pos < arr.length; pos++) {
    diag.push(arr[pos][pos]);
  }

  return diag;
}

const rotate = arr => {
  let rotated = [];

  for (let x = 0; x < arr.length; x++) {
    let row = arr[x];
    for (let y = 0; y < row.length; y++) {
      if (rotated[y] === undefined) {
        rotated[y] = [];
      }

      rotated[y][x] = row[y];
    }
  }

  return rotated;
}

const isVictory = (player, board) => {
  let horizontalLines = board;
  let verticalLines = rotate(board);
  let diagonalLines = [diagonal(horizontalLines), diagonal(verticalLines.reverse())];
  let lines = [...horizontalLines, ...verticalLines, ...diagonalLines];
  let isVictory = false;
  let winningLine = Array(board.length).fill(player);

  for (let line of lines) {
    if (compare(line, winningLine)) {
      isVictory = true;
    }
  }

  return isVictory;
};

const flatten = arr => arr.reduce((acc, arr) => arr.concat(acc), [])

const isDraw = board => {
  let flat = flatten(board);
  return flat.filter(x => x !== null).length === flat.length
}

const gameAI = AI({
  enumerate: (G, ctx) => {
    let moves = [];
    G.cells.map(
      (row, x) => row.map(
        (cell, y) => cell === null ? moves.push({move: "clickCell", args: [x, y]}) : undefined));

    console.log(moves)

    return moves;
  }
});

const TicTacToe = Game({
  setup: () => ({ cells: Array(3).fill(Array(3).fill(null)) }),

  moves: {
    clickCell(G, ctx, x, y) {
      let cells = dcopy(G.cells);
      if (cells[x][y] === null) {
        cells[x][y] = ctx.currentPlayer;
      }
      return { ...G, cells };
    },
  },
  flow: {
    endGameIf: (G, ctx) => {
      if (isVictory(ctx.currentPlayer, G.cells)) {
        return { winner: ctx.currentPlayer };
      } else if (isDraw(G.cells)) {
        return { draw: true };
      }
    },
    movesPerTurn: 1
  }
});

const App = Client({ game: TicTacToe, board: TicTacToeBoard, ai: gameAI });

export default App;


