import React from 'react';

const formatWinner = win => {
    if (win.draw) {
      return "Draw!";
    } else {
      return `Player ${win.winner} won!`;
    }
}

export default class TicTacToeBoard extends React.Component {
  onClick(x, y) {
    if (this.isActive(x, y)) {
      this.props.moves.clickCell(x, y);
      this.props.events.endTurn();
    }
  }

  isActive(x, y) {
    if (!this.props.isActive) return false;
    if (this.props.G.cells[x][y] !== null) return false;
    return true;
  }

  render() {
    let winner = '';
    if (this.props.ctx.gameover !== undefined) {
      winner = <div>Winner: {formatWinner(this.props.ctx.gameover)}</div>;
    }

    let turn = '';
    if (this.props.ctx.turn !== undefined) {
        turn = <div>Turn: {this.props.ctx.turn}</div>;
    }

    const cellStyle = {
      border: '1px solid #555',
      width: '50px',
      height: '50px',
      lineHeight: '50px',
      textAlign: 'center'
    };

    let tbody = this.props.G.cells.map((row, x) => 
        <tr key={x}>{
            row.map((cell, y) => 
                <td style={cellStyle} key={`${x},${y}`} onClick={() => this.onClick(x, y)}>
                    {this.props.G.cells[x][y]}
                </td>)
        }</tr>);

    return (
      <div>
        <table id="board">
          <tbody>{tbody}</tbody>
        </table>
        {winner}
        {turn}
      </div>
    );
  }
}


